
  
  .hero-containerq {
    background: url('../../public/images/legendarios.jpeg');
    background-position:center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }
  
  .hero-containerq  h1 {
    color: rgb(118, 114, 110);
    font-size: 90px;
    margin-top: -100px;
    font-family: 'Yanone Kaffeesatz', sans-serif;

  }

  
  .hero-containerq > .hero-title {
    color: rgb(109, 114, 144);
    font-size: 80px;
    margin-top: -40px;
    font-family: 'Roboto', sans-serif;

    

  }
  .hero-containerq img {
    height: 20em;
    margin-bottom: 25em;
  }
  
 
  .hero-containerq > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .hero-btns {
    margin-top: 32px;
  }
  
  .hero-btns .btn {
    margin: 6px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .hero-containerq > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .hero-containerq > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .hero-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }